.main-block{
  width:50vw;
  min-width:500px;
  height: 300px;
}
.content-block{
  width:100%;
  min-height: 200px;
  margin: 25px 0;
  padding-bottom: 15%;
}
.block-in-title{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.selector{
  display:flex;
  flex-direction: row;
  height: 100%;
  flex: 2 0 auto;
  justify-content: space-between;
}
.search-block{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height:40px;
  width:auto;
}
.search-block input{
  height:100%;
  flex: 4 0 auto;
  margin-right: 5px;
}
.selector-elem{
  height:100%;
  border:1px #1b877e solid;
  color: #1b877e;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left:2%;
  flex:1 0 auto;
}
.add-button{
  background-color: #1b877e;
  color:black;
  height:40px;
  display: flex;
  width:35%;
  min-width:270px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}
.add-block{
  position:fixed;
  top:90%;
  height: 10%;
  width: 50%;
  min-width:500px;
}
.backgroundPaper{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height:100%;
  width:100%;
}

.add-form{
  width:60%;
}
.item-of-list{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  margin:5px 0;
  min-height:50px;
}
.item-of-list span{
  word-wrap:break-word;
  overflow:hidden;
}
.button-to-do{
  display: flex;
  flex-direction: row;
  align-items: center;
}
@media screen and (max-width : 700px) {
  .main-block{
    width:70vw;
    height: 300px;
  }
  .block-in-title{
    margin:0 15px;
  }
  .add-block{
    width: 67vw;
  }
  .add-button{
    width:35%;
  }
}